.root {
  margin-top: 7vh;
  height: 93vh;
}

.imageContainer {
  width: 250px;
  height: 280px;
  border-radius: 50%;
  overflow: hidden;
  margin: 1rem;
}

.left-header {
  position: sticky;
  top: 0;
  left: 0;
  color: #fff;
  height: 100%;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, .5), rgba(0, 0, 0, .5)), url('../../assets/backgrounds/background2.jpg');
  background-size: cover;
  padding: 4rem;
  display: flex;
  align-items: end;
  justify-content: space-between;
  flex-direction: column;
}

.left-header::before {
  background-color: #fff;
  position: absolute;
  content: '';
  bottom: 50px;
  left: 30px;
  margin: 0 1rem;
  width: 80%;
  height: 2px;
}

.left-header::after {
  background-color: #fff;
  position: absolute;
  content: '';
  bottom: 50px;
  left: 30px;
  margin: 0 1rem;
  height: 80%;
  width: 2px;
}

.left-header h2 {
  margin-left: 4rem;
}

.v-center-box {
  min-height: 100%;
}

.sub-heading {
  font-size: .8rem;
  font-weight: 700;
  letter-spacing: .15rem;
  word-spacing: .05rem;
}

.about-data {
  position: relative;
}

.about-data::before {
  position: absolute;
  content: '\003c/ ABOUT \003e';
  top: 0;
  left: 50%;
  transform: translate(-50%, -60%);
  text-align: center;
  font-size: .8rem;
  font-weight: 900;
  color: #dee2e6;
  background-color: #fff;
  width: 8rem;
}

.about-data i {
  color: #f12d2d;
}

.about-data p {
  font-size: .7rem;
  letter-spacing: .05rem;
  word-spacing: .2rem;
}

.about-data .data-number {
  color: #494949;
  font-size: 1.2rem;
}

.icon-18 {
  font-size: 1.8rem;
}

@media (min-width: 992px) and (max-width: 1199px) {
  .left-header::before {
    width: 70%;
  }
}

@media (min-height: 590px) {
  .left-header::after {
    height: 85%;
  }
}

.hexagon-wrapper {
  display: inline-block;
  position: relative;
  width: 200px;
  /* Adjust the size as needed */
  height: 200px;
  /* Adjust the size as needed */
}

.hexagon {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
}

.hexagon img {
  width: 100%;
  height: auto;
  transform: translateY(-50%);
}