.container {
  height: 100%;
  text-align: center;
  margin: 0px 0px 0px 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 32px;
  font-weight: 200;
}

.nameContainer {
  color: red;
  font-weight: 500;
}

.buttonContainer {
  border: 1px solid #ffff;
  border-radius: 5px;
  padding: 5px 5%;
  cursor: pointer;
  transition: color 0.5s, border-color 0.5s;
}

.buttonContainer:hover {
  color: red;
  border: 1px solid red;
}